import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    currentUser: {
        userId: "",
        accessToken: "",
        // username:"",
        role: "",
        firstName: "",
        lastName: "",
        email: "",
        contact: ""
    }
}
const userSlice = createSlice({
    name: 'user',
    initialState,
    reducers: {
        login: (state, action) => {
            state.currentUser = action.payload
        },
        logout: (state) => {
            state.currentUser = initialState.currentUser
        },
        updateUser: (state, action) => {
            state.currentUser.role = action.payload.role
            state.currentUser.userId = action.payload.userId
            state.currentUser.firstName = action.payload.firstName
            state.currentUser.lastName = action.payload.lastName
            state.currentUser.email = action.payload.email
            state.currentUser.contact = action.payload.contact
        },
    }
})

export const { login, logout, updateUser } = userSlice.actions;

export default userSlice;