import React, {lazy, Suspense} from "react";
import {
    Navigate,
    Route,
    Routes
} from "react-router-dom";
import Loader from "../components/Loader/index.js";
const Home = lazy(()=>import("../screens/Home"));
const Layout = lazy(()=>import("../Layout"));
const SingleProduct = lazy(()=>import("../screens/SingleProduct"));
const AddEditProduct = lazy(()=>import("../screens/EmbarkAdmins/AddEditProduct"));
const Products = lazy(()=>import("../screens/Products"));
const Cart = lazy(()=>import("../screens/Cart"));
const Tnc = lazy(()=>import("../screens/Tnc"));
const Returns = lazy(()=>import("../screens/Returns"));
const AboutUs = lazy(()=>import("../screens/AboutUs"));
const Faq = lazy(()=>import("../screens/Faq"));
const SignIn = lazy(()=>import("../screens/SignIn"));
const SignUp = lazy(()=>import("../screens/SignUp"));
const MyAccount = lazy(()=>import("../screens/MyAccount"));
const Addresses = lazy(()=>import("../screens/MyAccount/addresses"));
const AddEditAddresses = lazy(()=>import("../screens/MyAccount/addEditAddress"));
const Orders = lazy(()=>import("../screens/MyAccount/orders"));
// const WishList = lazy(()=>import("../screens/MyAccount/wishList"));
const EditAccount = lazy(()=>import("../screens/MyAccount/editAccount"));
const EditAddress = lazy(()=>import("../screens/MyAccount/editAddress"));
// const CreateGiftSet = lazy(()=>import("../screens/CreateGiftSet"));
const CheckOut = lazy(()=>import("../screens/CheckOut"));
const ProductOrders = lazy(()=>import("../screens/EmbarkAdmins/Orders"));
const ManageProducts = lazy(()=>import("../screens/EmbarkAdmins/ManageProducts"));
const AddEditBanner = lazy(()=>import("../screens/EmbarkAdmins/AddEditBanner"));
const OrderView = lazy(()=>import("../screens/EmbarkAdmins/OrderView"));
const PageNotFound = lazy(()=>import("../screens/PageNotFound"));
const PerfumeStory = lazy(()=>import("../screens/PerfumeStory"));
const ContactUs = lazy(()=>import("../screens/ContactUs"));
const ProtectedComponent = lazy(()=>import("./ProtectedComponent"));
const ForgetPassword = lazy(()=>import("../screens/ForgetPassword"));
const ResetPassword = lazy(()=>import("../screens/ResetPassword"));
const SingleOrder = lazy(()=>import("../screens/MyAccount/singleOrder"));
// const AddCoupon = lazy(()=>import("../screens/EmbarkAdmins/AddCoupon"));
const ManageBanners = lazy(()=>import("../screens/EmbarkAdmins/ManageBanners"));
const AddEditPopup = lazy(()=>import("../screens/EmbarkAdmins/AddEditPopup"));
const ManagePopups = lazy(()=>import("../screens/EmbarkAdmins/ManagePopups"));
const PrivacyPolicy = lazy(()=>import("../screens/PrivacyPolicy"));

const permission = require("../helpers/permission.js");
const Components = () => {
    return (
        <>
            <Suspense fallback={<Loader/>}>
                <Routes>
                    <Route
                        path="/"
                        element={<Layout component={<Home />} />}
                    />
                    <Route
                        path="/product/:name"
                        element={<Layout component={<SingleProduct />} />}
                    />
                    <Route
                        path="/manage-products"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<ManageProducts />} />} />}
                    />
                    <Route
                        path="/add-product"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<AddEditProduct type="add" />} />} />}
                    />
                    <Route
                        path="/edit-product/:name"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<AddEditProduct type="edit" />} />} />}
                    />
                    <Route
                        path="/manage-banners"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<ManageBanners />} />} />}
                    />
                    <Route
                        path="/add-popup"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<AddEditPopup type="add" />} />} />}
                    />
                    <Route
                        path="/edit-popup/:id"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<AddEditPopup type="edit" />} />} />}
                    />
                    <Route
                        path="/manage-popups"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<ManagePopups />} />} />}
                    />
                    <Route
                        path="/add-banner"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<AddEditBanner type="add" />} />} />}
                    />
                    <Route
                        path="/edit-banner/:id"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<AddEditBanner type="edit" />} />} />}
                    />
                    {/* <Route
                        path="/add-coupon"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<AddCoupon type="add" />} />} />}
                    /> */}
                    <Route
                        path="/search"
                        element={<Layout component={<Products type="search" />} />}
                    />
                    <Route
                        path="/products/:name"
                        element={<Layout component={<Products type="hierarchy" />} />}
                    />
                    <Route
                        path="/fragrances/:name"
                        element={<Layout component={<Products type="fragrances" />} />}
                    />
                    <Route
                        path="/gender/:name"
                        element={<Layout component={<Products type="gender" />} />}
                    />
                    <Route
                        path="/cart"
                        element={<Layout component={<Cart />} />}
                    />
                    <Route
                        path="/sign-in"
                        element={<Layout component={<SignIn />} />}
                    />
                    <Route
                        path="/sign-up"
                        element={<Layout component={<SignUp />} />}
                    />
                    <Route
                        path="/my-account"
                        element={<Layout component={<MyAccount />} />}
                    >
                        <Route index element={<Navigate to={"/my-account/edit-account"} />} />
                        <Route
                            path="/my-account/edit-account"
                            element={<ProtectedComponent accessCode={permission.ALLOW_ALL_ROLES} Component={<EditAccount />} />}
                        />
                        <Route
                            path="/my-account/addresses"
                            element={<ProtectedComponent accessCode={permission.ALLOW_ALL_ROLES} Component={<Addresses />} />}
                        />
                        <Route
                            path="/my-account/orders"
                            element={<ProtectedComponent accessCode={permission.ALLOW_ALL_ROLES} Component={<Orders />} />}
                        />
                        <Route
                            path="/my-account/orders/:id"
                            element={<ProtectedComponent accessCode={permission.ALLOW_ALL_ROLES} Component={<SingleOrder />} />}
                        />
                        {/* <Route
                            path="/my-account/wishlist"
                            element={<ProtectedComponent accessCode={permission.ALLOW_ALL_ROLES} Component={<WishList />} />}
                        /> */}
                        <Route
                            path="/my-account/addresses/:addressType"
                            element={<ProtectedComponent accessCode={permission.ALLOW_ALL_ROLES} Component={<EditAddress />} />}
                        />
                    </Route>
                    <Route
                        path="/addEditAddress/:addressType"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ALL_ROLES} Component={<Layout component={<AddEditAddresses />} />} />}
                    />
                    {/* <Route
                        path="/create-gift-set"
                        element={<Layout component={<CreateGiftSet />} />}
                    /> */}
                    <Route
                        path="/terms-and-conditions"
                        element={<Layout component={<Tnc />} />}
                    />
                    <Route
                        path="/return-policy"
                        element={<Layout component={<Returns />} />}
                    />
                    <Route
                        path="/about-us"
                        element={<Layout component={<AboutUs />} />}
                    />
                    <Route
                        path="/faq"
                        element={<Layout component={<Faq />} />}
                    />
                    <Route
                        path="/checkout"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ALL_ROLES} Component={<Layout component={<CheckOut />} />} />}
                    />
                    <Route
                        path="/all-orders"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<ProductOrders />} />} />}
                    />
                    <Route
                        path="/view-order/:id"
                        element={<ProtectedComponent accessCode={permission.ALLOW_ADMINS} Component={<Layout component={<OrderView />} />} />}
                    />
                    <Route
                        path="/perfume-story"
                        element={<Layout component={<PerfumeStory />} />}
                    />
                    <Route
                        path="/contact-us"
                        element={<Layout component={<ContactUs />} />}
                    />
                    <Route
                        path="/forget-password"
                        element={<Layout component={<ForgetPassword />} />}
                    />
                    <Route
                        path="/reset-password/:token"
                        element={<Layout component={<ResetPassword />} />}
                    />
                    <Route
                        path="/privacy-policy"
                        element={<Layout component={<PrivacyPolicy />} />}
                    />
                    <Route
                        path="*"
                        element={<Layout component={<PageNotFound />} />}
                    />
                </Routes>
            </Suspense>
        </>
    )
}

export default Components;